                    <form>
                      <div class="mb-3">
                        <input type="text" class="form-control" v-model="profileURL" placeholder="이미지 URL을 작성해주세요.">
                      </div>
                    </form>
                  </div>
                  <div class="modal-footer">
                    <button type="button" @click="changeProfile()" class="btn btn-primary" data-bs-dismiss="modal">Save</button>
                  </div>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>

      <div class="myPage2" v-if="myPageState === 1">
        <div>
          프로필이미지 수정
        </div>
        <button @click="myPageState = 0">1</button>
      </div>

      <div class="myPage3" style="height: auto" v-if="myPageState === 2">
        <div class="stable" style="display: flex; justify-content: space-between; align-items: center; position: sticky; top: 0; background: white; box-shadow: 0 2px 2px -2px rgba(0,0,0,0.2);">
          <div></div>
          <h1 style="text-align: center;">
            평가한 영화
          </h1>
          <div style="text-align: right; margin-right: 10px" @click="myPageState = 0">
            <svg xmlns="http://www.w3.org/2000/svg" height="16" width="12" viewBox="0 0 384 512"><!--!Font Awesome Free 6.5.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license/free Copyright 2024 Fonticons, Inc.--><path d="M376.6 84.5c11.3-13.6 9.5-33.8-4.1-45.1s-33.8-9.5-45.1 4.1L192 206 56.6 43.5C45.3 29.9 25.1 28.1 11.5 39.4S-3.9 70.9 7.4 84.5L150.3 256 7.4 427.5c-11.3 13.6-9.5 33.8 4.1 45.1s33.8 9.5 45.1-4.1L192 306 327.4 468.5c11.3 13.6 31.5 15.4 45.1 4.1s15.4-31.5 4.1-45.1L233.7 256 376.6 84.5z"/></svg>
          </div>
        </div>
        <div>
          <div v-for="(item, i) in ratedMovie" :key="i" style="display: flex; margin: 10px">
            <router-link :to="`/movie/${item.movieId}`">
              <img :src="item.movieImg" alt="#" style="width: 100px; height: 150px; margin-right: 20px">
            </router-link>
            <div>
              <router-link :to="`/movie/${item.movieId}`" style="text-decoration: none; color: inherit;">
                <div style="font-weight: bold; font-size: 20px">{{ item.movieName }}</div>
              </router-link>
              <span>{{ item.movieYear }}</span>
              <span style="color: orange"> 평가함 ⭐{{ item.movieRate.toFixed(2) }}</span>
              <span style="color: gray; margin-left: 15px; opacity: 70%"> {{ item.RateCount }}</span>
              <div style="margin-top: 10px; margin-bottom: 10px">{{ item.commentDate }}</div>
              <div style="margin-top: 10px; margin-bottom: 10px">좋아요 {{ item.likeCount }}개</div>
              <div style="margin-top: 10px; margin-bottom: 10px">{{ item.commentContent }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import {mapMutations, mapState} from "vuex";

export default {
  name: 'MyPage-MyPage',
  components: {
  },
  data() {
    return {
      ratedMovie : [],
      myPageState: 0,
      profileURL: '',
    }
  },
  computed: {
    ...mapState(['loginState', 'currentIdInfo', 'user', 'movies']),
    userProfileImage() {
      if (this.user[this.currentIdInfo.idNum].profileImage == " ") {
        return 'https://img1.daumcdn.net/thumb/R1280x0/?scode=mtistory2&fname=https%3A%2F%2Fblog.kakaocdn.net%2Fdn%2FbyPacn%2FbtsCK8TTd45%2Fe2MFQFo7AkudR44TKM8V5k%2Fimg.png';
      }
      return this.user[this.currentIdInfo.idNum].profileImage;
    }
  },
  methods: {
    ...mapMutations(['loginM','logoutM']),
    changeProfile() {
      this.user[this.currentIdInfo.idNum].profileImage = this.profileURL;
      this.profileURL = '';
    }
  },
  mounted() {
      for (let i = 0; i < this.movies.length; i++) {
        for (let j = 0; j < this.movies[i].movieComment.length; j++) {
          if (this.movies[i].movieComment[j].commentName === this.currentIdInfo.nickname) {
            this.ratedMovie.push({
              movieName: this.movies[i].movieName,
              movieImg: this.movies[i].movieImg,
              movieRate: this.movies[i].movieComment[j].commentRate,
              commentDate: this.movies[i].movieComment[j].commentDate,
              likeCount: this.movies[i].movieComment[j].commentLike,
              commentContent: this.movies[i].movieComment[j].commentContent,
              movieId: this.movies[i].movieId,
            });
          }
        }
      }


    },
}

</script>

