<script>
import {mapMutations, mapState} from "vuex";
export default {
  name: 'App',
  data() {
    return {
      searchbarState: false,
      createIdState: 0,
      pwFind: 0,
      loginIdInput: {
        id: '',
        pw: '',
      },
      createIdInput: {
        id: '',
        pw: '',
        name: '',
        birth: '',
        nickname: '',
      },
      passwordFindForm: {
        id: '',
        name: '',
        birth: '',
      },
      findPassword: '',
    }
  },
  components: {

  },
  computed: {
    ...mapState(['movies', "movieOriginal", 'searchState', 'loginState', 'currentIdInfo', 'user']),
    profileStyle() {
      if (this.user[this.currentIdInfo.idNum].profileImage == " ") {
        return {
          backgroundImage: "url(https://img1.daumcdn.net/thumb/R1280x0/?scode=mtistory2&fname=https%3A%2F%2Fblog.kakaocdn.net%2Fdn%2FbyPacn%2FbtsCK8TTd45%2Fe2MFQFo7AkudR44TKM8V5k%2Fimg.png)"
        }
      }
      return {
        backgroundImage: `url(${this.user[this.currentIdInfo.idNum].profileImage})`
      }
    }
  },
  methods: {
    ...mapMutations(['searchMovie', 'setMovie', 'changeSearchStateOn', 'createIdM', 'loginM']),
    finishCreateId() {
      setTimeout(() => {this.createIdState = 0}, 3000)
    },
    finishFindPassword() {
      this.findPassword= '';
      setTimeout(() => {this.pwFind = 0}, 3000)
    },
    openSearchBar() {
      this.searchbarState = true;
    },
    closeSearchBar() {
      this.searchbarState = false;
    },
    createId() {
      if (this.createIdState === 0) {
        const { id, pw, name, birth, nickname } = this.createIdInput;

        this.createIdM({ id, pw, name, birth, nickname });
        this.createIdState = 1;
      }
      else if (this.createIdState === 1) {
        this.createIdState = 0;
      }
    },
    login() {
      const { id, pw } = this.loginIdInput;
        this.loginM({ id, pw });
    },
    searchPassword(event) {
      event.preventDefault();  // Prevent page from being reloaded

      const userId = this.passwordFindForm.id;
      const userName = this.passwordFindForm.name;
      const userBirth = parseInt(this.passwordFindForm.birth);

      const userInfo = this.user;

      console.log(userId);
      console.log(userName);
      console.log(userBirth);
      console.log(userInfo);
      console.log(this.user);

      const user = userInfo.find(user => user.id === userId && user.name === userName && user.birth === userBirth);

      console.log(user);

      if (user) {
        // If matching user is found
        this.pwFind = 1;
        this.findPassword = user.password;
      } else {
        // If no matching user is found
        this.pwFind = 2;
      }
      console.log(this.pwFind);
    }
  },
  created() {
    this.changeSearchStateOn()
  },
}
</script>

