<script>
import {mapActions, mapMutations, mapState} from "vuex";

export default {
  name: 'Movie-Movie',
  data() {
    return {
      commentState: 0,
      active: [],
    }
  },
  components: {

  },
  computed: {
    ...mapState(['movies', 'user', 'sortedRanking', 'sortedRateCount', 'sortedMovieYear', 'movieOriginal', 'searchState', 'currentIdInfo']),
    siteId() {
      return this.$route.params.id
    },
    topEightComments() {
      // movieComment 배열을 복사한 뒤, commentLike 값에 따라 내림차순 정렬합니다.
      const sortedComments = [...this.movies[this.siteId].movieComment].sort((a, b) => b.commentLike - a.commentLike);
      // 정렬된 배열에서 최대 8개의 코멘트만 반환합니다.
      return sortedComments.slice(0, 8);
    },
    topEightCommentsRecent() {
      const sortedComments = [...this.movies[this.siteId].movieComment].sort((a, b) => {
        // 날짜를 비교하려면 먼저 Date 객체로 변환해야 합니다.
        const dateA = new Date(a.commentDate.replace(".", "/"));
        const dateB = new Date(b.commentDate.replace(".", "/"));

        // JavaScript에서 날짜를 비교할 때 더 큰 값이 더 최근의 날짜를 나타냅니다.
        // 따라서 dateB - dateA로 계산하면 최신 날짜 순서로 정렬됩니다.
        return dateB - dateA;
      });

      return sortedComments.slice(0, 8);
    }
  },
  methods: {
    ...mapMutations(['changeSearchStateOff', 'likeComment']),
    ...mapActions(['saveRating', 'saveComment']),
    profileImageStyle(nickname) {
      for (let user of this.user) {
        if (user.nickname === nickname) {
          if (user.profileImage === " ")
            return {
              backgroundImage: "url(https://img1.daumcdn.net/thumb/R1280x0/?scode=mtistory2&fname=https%3A%2F%2Fblog.kakaocdn.net%2Fdn%2FbyPacn%2FbtsCK8TTd45%2Fe2MFQFo7AkudR44TKM8V5k%2Fimg.png)"
            }
          else
          {
            return {
              backgroundImage: `url(${user.profileImage})`
            };
          }
        }
      }

      // nickname이 모든 user.name과 일치하지 않는 경우
      return {
        backgroundImage: "url(https://img1.daumcdn.net/thumb/R1280x0/?scode=mtistory2&fname=https%3A%2F%2Fblog.kakaocdn.net%2Fdn%2FbyPacn%2FbtsCK8TTd45%2Fe2MFQFo7AkudR44TKM8V5k%2Fimg.png)"
      };

    },
    liking(commentIdIndex) {
      //vuex 데이터를 여기서 변경하면 안되므로 데이터를 조종할때 vuex의 mutation을 사용.
      this.active[commentIdIndex] = !this.active[commentIdIndex];
      if (this.active[commentIdIndex]) {
        // console.log(this.siteId)
        // console.log(commentIdIndex)
        this.likeComment({ siteId2: this.siteId, commentIdIndex2: commentIdIndex, likeOrDislike: 0 });
      } else {
        // console.log(commentIdIndex)
        this.likeComment({ siteId2: this.siteId, commentIdIndex2: commentIdIndex, likeOrDislike: 1 });
      }
    },
    liking2(a) {
      this.active = !this.active;
      this.commentNum = a;
      console.log(this.active)
      if (this.active) {
        this.movies[this.siteId].movieComment[this.commentNum].commentLike += 1;
      } else {
        this.movies[this.siteId].movieComment[this.commentNum].commentLike -= 1;
      }
    },
    handleRatingSubmit(e) {
      e.preventDefault();

      const hasCommented = this.movies[this.siteId].movieComment.some(
          comment => comment.commentName === this.currentIdInfo.nickname
      ); // varies by your data structure, adjust as needed

      if (hasCommented) {
        alert("이미 평가를 한 영화입니다.");
        return;
      }

      if (this.currentIdInfo.nickname === undefined) {
        alert("로그인을 해주세요")
        return
      }

      if (e.target.reviewStar.value === "") {
        alert("별점을 선택해주세요")
        return
      }


      const commentText = document.getElementById('reviewContents').value;

      if (commentText) {
        // 현재 날짜와 시간을 아래와 같은 포맷으로 설정합니다: "2020/12/30 12:30:59"
        const now = new Date();
        const commentDate = now.getFullYear() + '.' + (now.getMonth()+1) + '.' + now.getDate() + '. ' + now.getHours() + ':' + now.getMinutes();
        const commentName = this.currentIdInfo.nickname;
        const radioButtonGroup = e.target.reviewStar;
        const formData = new FormData(e.target);
        const rating = Number(formData.get('reviewStar'));
        const commentRate = rating;

        for (let i = 0; i < radioButtonGroup.length; ++i) {
          radioButtonGroup[i].checked = false;
        }
        document.getElementById('reviewContents').value = '';
        alert("코멘트가 등록되었습니다");

        const newCommentId = this.movies[this.siteId].movieComment.length;

        this.saveComment({
          siteId: this.siteId,
          comment: { commentName:commentName, commentContent: commentText, commentLike: 0, commentDate: commentDate, commentId: newCommentId.toString(), commentRate: commentRate }
        });
      } else {
        alert("코멘트를 입력해주세요");
      }

      const formData = new FormData(e.target);
      const rating = Number(formData.get('reviewStar'));
      this.saveRating({rating: rating, siteId: this.siteId});

      console.log(this.movies[this.siteId].movieComment)
    },

    time(a) {
      const hour = Math.floor(a / 60);
      const min = a % 60;

      return `${hour}H ${min}M`
    },
    age(a) {
      if (a === 0) {
        return 'G'
      } else if (a === 12) {
        return 'PG-13'
      } else if (a === 15) {
        return 'R'
      } else if (a === 18) {
        return 'NC-17'
      }
    }
  },
  created() {
    this.changeSearchStateOff()
    this.active = Array(this.movies[this.siteId].movieComment.length).fill(false);
  },
}

</script>

