import { createStore } from 'vuex'
import Movie from "/public/movies.json";
import User from "/public/user.json";


const store = createStore({
    state() {
        return {
            movies: Movie,
            user: User,
            sortedRanking: [],
            sortedRateCount: [],
            sortedMovieYear: [],
            movieOriginal: [...Movie],
            searchState: true,
            loginState: false,
            currentIdInfo: '',
        }
    },
    actions: {
        saveRating({commit}, payload) {
            commit('SET_RATING', payload.rating);
            commit('UPDATE_MOVIE', payload);
        },
        saveComment({ commit }, payload) {
            commit('SAVE_COMMENT', payload);
        },
    },
    mutations: {
        loginM(state, { id, pw }) {
            let loginState = false;
            for (let i = 0; i < state.user.length; i++) {
                if (state.user[i].id === id && state.user[i].password === pw) {
                    state.loginState = true;
                    state.currentIdInfo = state.user[i];
                    loginState = true;
                    break;
                }
            }

            if (loginState === false)
                alert("아이디 또는 비밀번호가 틀렸습니다.")
        },
        logoutM(state) {
            state.loginState = false;
            state.currentIdInfoZ = '';
        },
        createIdM(state, { id, pw, name, birth, nickname }) {
            state.user.push({
                idNum: state.user.length,
                id: id,
                password: pw,
                name: name,
                birth: Number(birth),
                nickname: nickname,
                profileImage: " ",
            })
            console.log(state.user)
        },
        likeComment(state, { siteId2, commentIdIndex2, likeOrDislike }) {
            const movie = state.movies[siteId2];
            if (likeOrDislike === 0) movie.movieComment[commentIdIndex2].commentLike++;
            else movie.movieComment[commentIdIndex2].commentLike--;
            state.movies.splice(siteId2, 1, movie);
        },
        SAVE_COMMENT(state, payload) {
            state.movies[payload.siteId].movieComment.push(payload.comment);
        },
        SET_RATING(state, rating) {
            state.movieRating = rating;  // movieRating은 저장하려는 별점을 보관하는 state
        },
        SET_COMMENTID(state, commentId) {
            state.commentId = commentId;
        },
        UPDATE_MOVIE(state, {rating, siteId}) {
            // state.movies[siteId].RateCount++;
            // state.movies[siteId].movieRate = (state.movies[siteId].movieRate *
            //         (state.movies[siteId].RateCount - 1) + rating) /
            //     state.movies[siteId].RateCount;
            // state.movies = {...state.movies};
            const movie = state.movies[siteId];
            movie.RateCount++;
            movie.movieRate = (movie.movieRate * (movie.RateCount - 1) + rating) / movie.RateCount;
            state.movies.splice(siteId, 1, movie);  // 바뀐 영화 객체로 배열 항목을 교체합니다.
        },
        changeSearchStateOn(state) {
            state.searchState = true
        },
        changeSearchStateOff(state) {
            state.searchState = false
        },
        setMovie(state) {
            state.movies = [...state.movieOriginal];
        },
        sortRanking(state) {
            let copy = [...state.movies];
            copy.sort(function (a, b) {
                return b.movieRate - a.movieRate;
            });

            state.sortedRanking = [...copy];
        },

        sortRateCount(state) {
            let copy = [...state.movies];
            copy.sort(function (a, b) {
                return b.RateCount - a.RateCount;
            });

            state.sortedRateCount = [...copy];
        },

        sortYear(state) {
            let copy = [...state.movies];
            copy.sort(function (a, b) {
                return b.movieYear - a.movieYear;
            });

            state.sortedMovieYear = [...copy];
        },
        searchMovie(state, data) {
            state.movies = state.movieOriginal.filter((a) => {
                return a.movieName.toLowerCase().includes(data.toLowerCase());
            })
        },
    },
})
export default store;