<template>
  <div class="carousel1">
    <div class="carousel_wrapper d-flex" style="margin: 10px">
      <div v-for="(item, i) in rateCountPage" :key="i" class="carousel_slide">
        <div class="carousel_title" v-if="i === 0" style="font-weight: bold; font-size: 28px">인기순</div>
        <div v-else style="font-weight: bold; font-size: 28px; color: rgba(0,0,0,0)">가장 많은 평가순</div>
        <router-link :to="`/movie/${item.movieId}`">
          <img :src="item.movieImg" alt="#" class="carousel_img"/>
        </router-link>
        <button @click="prevPage(0)" type="button" v-if="i%5 === 0" class="btn btn-secondary btn-lg nav-button nav-button-left" style="left: -30px;">&lt;</button>
        <button @click="nextPage(0)" type="button" v-if="(i+1)%5 === 0" class="btn btn-secondary btn-lg nav-button nav-button-right" style="right: -30px">&gt;</button>

        <div style="font-weight: bold; font-size: 20px">{{ item.movieName }}</div>
        <span>{{ item.movieYear }}</span>
        <span style="color: orange"> ⭐{{ item.movieRate.toFixed(2) }}</span>
        <span style="color: gray; margin-left: 15px; opacity: 70%"> {{ item.RateCount }}명</span>

      </div>
    </div>
  </div>

  <div class="carousel2">
    <div class="carousel_wrapper d-flex" style="margin: 10px">
      <div v-for="(item, i) in rankingPage" :key="i" class="carousel_slide">
        <div class="carousel_title" v-if="i === 0" style="font-weight: bold; font-size: 28px">가장 높은 평가순</div>
        <div v-else style="font-weight: bold; font-size: 28px; color: rgba(0,0,0,0)">가장 많은 평가순</div>
        <router-link :to="`/movie/${item.movieId}`">
          <img :src="item.movieImg" alt="#" class="carousel_img"/>
        </router-link>        <button @click="prevPage(1)" type="button" v-if="i%5 === 0" class="btn btn-secondary btn-lg nav-button nav-button-left" style="left: -30px;">&lt;</button>
        <button @click="nextPage(1)" type="button" v-if="(i+1)%5 === 0" class="btn btn-secondary btn-lg nav-button nav-button-right" style="right: -30px">&gt;</button>

        <div style="font-weight: bold; font-size: 20px">{{ item.movieName }}</div>
        <span>{{ item.movieYear }}</span>
        <span style="color: orange"> ⭐{{ item.movieRate.toFixed(2) }} </span>
        <span style="color: gray; margin-left: 15px; opacity: 70%"> {{ item.RateCount }}명</span>
      </div>
    </div>
  </div>

  <div class="carousel3">
    <div class="carousel_wrapper d-flex" style="margin: 10px">
      <div v-for="(item, i) in movieYearPage" :key="i" class="carousel_slide">
        <div class="carousel_title" v-if="i === 0" style="font-weight: bold; font-size: 28px">최신 영화 순</div>
        <div v-else style="font-weight: bold; font-size: 28px; color: rgba(0,0,0,0)">가장 많은 평가순</div>
        <router-link :to="`/movie/${item.movieId}`">
          <img :src="item.movieImg" alt="#" class="carousel_img"/>
        </router-link>        <button @click="prevPage(2)" type="button" v-if="i%5 === 0" class="btn btn-secondary btn-lg nav-button nav-button-left" style="left: -30px;">&lt;</button>
        <button @click="nextPage(2)" type="button" v-if="(i+1)%5 === 0" class="btn btn-secondary btn-lg nav-button nav-button-right" style="right: -30px">&gt;</button>

        <div style="font-weight: bold; font-size: 20px">{{ item.movieName }}</div>
        <span>{{ item.movieYear }}</span>
        <span style="color: orange"> ⭐{{ item.movieRate.toFixed(2) }}</span>
        <span style="color: gray; margin-left: 15px; opacity: 70%"> {{ item.RateCount }}명</span>

      </div>
    </div>
  </div>
</template>

<script>

import {mapMutations, mapState} from "vuex";

export default {
  name: 'Home-Home',
  data() {
    return {
      currentPage0: 0,
      itemsPerPage0: 5,
      currentPage1: 0,
      itemsPerPage1: 5,
      currentPage2: 0,
      itemsPerPage2: 5,
    }
  },
  components: {

  },
  computed: {
    ...mapState(['movies', 'user', 'sortedRanking', 'sortedRateCount', 'sortedMovieYear', 'movieOriginal', 'searchState']),
    rateCountPage () {
      let start = this.currentPage0 * this.itemsPerPage0;
      let end = start + this.itemsPerPage0;
      return this.sortedRateCount.slice(start, end);
    },
    rankingPage() { // 현재 페이지 아이템만 반환하는 추가된 계산된 속성
      let start = this.currentPage1 * this.itemsPerPage1;
      let end = start + this.itemsPerPage1;
      return this.sortedRanking.slice(start, end);
    },
    movieYearPage () {
      let start = this.currentPage2 * this.itemsPerPage2;
      let end = start + this.itemsPerPage2;
      return this.sortedMovieYear.slice(start, end);
    },
  },
  methods: {
    ...mapMutations(['sortYear', 'sortRanking', 'sortRateCount', 'changeSearchStateOn']),
    nextPage(a) {
      if (a === 0) {
        if ((this.currentPage0 + 1) * this.itemsPerPage0 < this.sortedRanking.length) {
          this.currentPage0++;
        }
      }
      if (a === 1) {
        if ((this.currentPage1 + 1) * this.itemsPerPage1 < this.sortedRanking.length) {
          this.currentPage1++;
        }
      }
      if (a === 2) {
        if ((this.currentPage2 + 1) * this.itemsPerPage2 < this.sortedRanking.length) {
          this.currentPage2++;
        }
      }
    },
    prevPage(a) {
      if (a === 0) {
        if (this.currentPage0 > 0) {
          this.currentPage0--;
        }
      }
      else if (a === 1) {
        if (this.currentPage1 > 0) {
          this.currentPage1--;
        }
      }
      else if (a === 2) {
        if (this.currentPage2 > 0) {
          this.currentPage2--;
        }
      }
    }
  },
  created() {
    this.sortRanking();
    this.sortRateCount();
    this.sortYear();
    this.changeSearchStateOn()
  }
}
</script>

